import { FC } from 'react';
import styled from 'styled-components';

const StyledCubicCorner = styled.div<any>`
  height: ${(p) => p?.height ?? 0}px;
  width: ${(p) => p?.width ?? 0}px;
  overflow: visible;
  position: relative;
  & > svg.box-svg {
    overflow: visible;
    z-index: 1;
    position: absolute;
    top: -1px;
    left: -1px;
  }
  & > div.content {
    z-index: 2;
    background: transparent;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
`;

export const CubicCorner: FC<any> = ({
  children,
  w = 300,
  h = 200,
  r = 40,
  b = 1,
  p = 0.25,
  sc = '#000',
  bg = '#eee',
}) => {
  const lh = w - 2 * r;
  const lw = h - 2 * r;
  const br = r - b / 2;

  const svg = (
    <svg
      className="box-svg"
      width={w + 2}
      height={h + 2}
      strokeWidth={b}
      stroke={sc}
      fill={bg}
    >
      <path
        d={`
    M ${r + 1} ${b / 2 + 1}
    l ${lh} 0 
    c ${br * (1 - p)} 0, ${br} ${br * p}, ${br} ${br} 
    l 0 ${lw}
    c 0 ${br * (1 - p)}, ${-br * p} ${br}, ${-br} ${br}
    l ${-lh} 0
    c ${-br * (1 - p)} 0, ${-br} ${-br * p}, ${-br} ${-br}
    l 0 ${-lw}
    c 0 ${-br * (1 - p)}, ${br * p} ${-br}, ${br} ${-br}
    Z
    `}
      />
    </svg>
  );

  return (
    <StyledCubicCorner style={{ width: w, height: h }}>
      {svg}
      <div className="content">{children}</div>
    </StyledCubicCorner>
  );
};
