import create from 'flooks';
import { useEffect, useState } from 'react';
import { defaultStorage, IndexedDBStorage } from '../utils/db';

/**
 *  create  IndexedDB
 * @param storage IndexedDBStorage 
 * @param storeInfo  flooksStore 
 */
export const createStoreWidthDb = (
  storage: IndexedDBStorage<any>,
  storeInfo: any
) => {
  const createProps = (flooksStore: any) => {
    const _store = { ...storeInfo };
    const valueKeys: string[] = [];

    for (const key in storeInfo) {
      if (typeof storeInfo[key] === 'function') {
        _store[key] = storeInfo[key](flooksStore);
      } else {
        valueKeys.push(key);
        _store['set' + key[0].toUpperCase() + key.slice(1)] = async (
          value: any,
          save: boolean = true
        ) => {
          const preValue = flooksStore()[key];
          const newValue =
            typeof value === 'function' ? value(preValue) : value;
          if (save) {
            await storage.setItem(key, newValue);
          }
          flooksStore({ [key]: newValue });
        };
      }
    }

    _store.initFromDb = async () => {
      if (valueKeys.length === 0) return;
      const values = await Promise.all(
        valueKeys.map(async (key) => {
          const value = await storage.getItem(key);
          return [key, value];
        })
      );
      values.forEach(([key, value]) => {
        if (value !== undefined && value !== null) {
          flooksStore({ [key]: value });
        }
      });
    };

    return _store;
  };

  const useStoreWidthDb: any = create(createProps);

  return (waitForInit = false) => {
    const [initFinished, setInitFinished] = useState(false);
    const states = useStoreWidthDb();
    const { initFromDb } = states;
    useEffect(() => {
      if (!waitForInit) {
        setInitFinished(true);
        return;
      }
      if (initFinished || !states) return;
      const init = async () => {
        try {
          await initFromDb();
          setInitFinished(true);
          console.log('[data] init finished');
        } catch (error) {
          console.error('[data] failed to init states from DB', error);
        }
      };
      init();
    }, [initFinished, initFromDb, states, waitForInit]);
    return [!initFinished, states];
  };
};

export const createStoreWidthDefaultDb = (storeInfo: any) => {
  return createStoreWidthDb(defaultStorage, storeInfo);
};
