type Key = string | number;

export type IndexedDBStorage<T> = {
  getItem(key: string): Promise<T | null>;
  setItem(key: string, value: T): Promise<void>;
  removeItem(key: string): Promise<void>;
  clear: () => Promise<void>;
};


export interface Options {
  dbName: string;
  storeName: string;
}

export const createIndexedDBStorage = <T>(options: Options): IndexedDBStorage<T> => {
  let db: IDBDatabase | null = null;

  const open = (): Promise<void> => new Promise((resolve, reject) => {
    const request = window.indexedDB.open(options.dbName, 1);

    request.onerror = (event: Event) => {
      reject(new Error(`Failed to open IndexedDB: ${(event.target as any).errorCode}`));
    };

    request.onsuccess = (event: Event) => {
      db = (event.target as IDBOpenDBRequest).result;
      resolve();
    };

    request.onupgradeneeded = (event: IDBVersionChangeEvent) => {
      db = (event.target as IDBOpenDBRequest).result;
      db.createObjectStore(options.storeName, { keyPath: "key" });
    };
  });

  const getItem = async (key: Key): Promise<any | null> => {
    await open();

    return new Promise((resolve, reject) => {
      const transaction = db!.transaction([options.storeName], "readonly");
      const store = transaction.objectStore(options.storeName);
      const request = store.get(key);

      request.onerror = (event: Event) => {
        reject(new Error(`Failed to get item from IndexedDB: ${(event.target as any).error}`));
      };

      request.onsuccess = () => {
        resolve(request.result ? request.result.value : null);
      };
    });
  };

  const setItem = async (key: Key, value: any): Promise<void> => {
    await open();

    return new Promise((resolve, reject) => {
      const transaction = db!.transaction([options.storeName], "readwrite");
      const store = transaction.objectStore(options.storeName);
      const request = store.put({ key, value });

      request.onerror = (event: Event) => {
        reject(new Error(`Failed to set item in IndexedDB: ${(event.target as any).error}`));
      };

      request.onsuccess = () => {
        resolve();
      };
    });
  };

  const removeItem = async (key: Key): Promise<void> => {
    await open();

    return new Promise((resolve, reject) => {
      const transaction = db!.transaction([options.storeName], "readwrite");
      const store = transaction.objectStore(options.storeName);
      const request = store.delete(key);

      request.onerror = (event: Event) => {
        reject(new Error(`Failed to remove item from IndexedDB: ${(event.target as any).error}`));
      };

      request.onsuccess = () => {
        resolve();
      };
    });
  };

  const clear = async (): Promise<void> => {
    await open();

    return new Promise((resolve, reject) => {
      const transaction = db!.transaction([options.storeName], "readwrite");
      const store = transaction.objectStore(options.storeName);
      const request = store.clear();

      request.onerror = (event: Event) => {
        reject(new Error(`Failed to clear IndexedDB: ${(event.target as any).error}`));
      };
      request.onsuccess = () => {
        resolve();
      };
    });
  };

  return {
    getItem: (key: Key) => getItem(key),
    setItem: (key: Key, value: any) => setItem(key, value),
    removeItem: (key: Key) => removeItem(key),
    clear: () => clear()
  };
};

export const defaultStorage: IndexedDBStorage<any> =
  createIndexedDBStorage<any>({
    dbName: 'donut-db-0.1',
    storeName: 'donut-store-0.1',
  });
