import { useRef } from 'react';

export * from './dbHook';
export * from './storeHook';

export const useDinamicCallBack = () => {
  const ref = useRef<(() => any) | null>(null);
  const setCallBack = (callBack: () => any) => {
    if (callBack) {
      ref.current = callBack;
    }
  };

  const runCallBack = () => {
    if (typeof ref.current === 'function') {
      try {
        ref.current?.();
      } catch (error) {
        console.error(error);
      }
    }
  };
  return [runCallBack, setCallBack] as any[];
};
