import { FC, useContext, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { Loading } from './loading';
import { LoopScroller } from './loop-scroll/loop-scroll';
import { CallBackContext, useStore } from './main';
import { markFive, markPointsStatus } from '../utils/misc';
// import _ from 'lodash';

const gridLineColor = '#aaa';

export const StyledPoint = styled.div<any>`
  height: ${(p) => p?.pointSize ?? 50}px;
  width: ${(p) => p?.pointSize ?? 50}px;
  background: linear-gradient(
        transparent,
        transparent calc(50% - 1px),
        ${gridLineColor} calc(50% - 1px),
        ${gridLineColor} calc(50% + 1px),
        transparent calc(50% + 1px),
        transparent 100%
      )
      no-repeat,
    linear-gradient(
        90deg,
        transparent,
        transparent calc(50% - 1px),
        ${gridLineColor} calc(50% - 1px),
        ${gridLineColor} calc(50% + 1px),
        transparent calc(50% + 1px),
        transparent 100%
      )
      no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  ${(p) => p?.gameEnd && !p?.marked && 'opacity: 0.3;'}
  ${(p) => p?.gameEnd && p?.marked && 'background: transparent;'}
`;

const pieceHover = (p: any) => `
  &:hover {
    opacity: 0.5;
    background-color: ${
      p?.next === 1 ? '#000' : p?.next === -1 ? '#fff' : 'transparent'
    };
    border-color: black;
  }
`;

export const Stone = styled.div<any>`
  box-sizing: border-box;
  height: 90%;
  width: 90%;
  border-radius: 100%;
  z-index: 10;
  background-color: ${(p) =>
    p?.status === 1 ? 'black' : p?.status === -1 ? 'white' : 'transparent'};
  border: ${(p) => p?.borderSize ?? 2}px solid black;
  border-color: ${(p) =>
    p?.marked ? '#dd3' : p?.status !== 0 ? '#000' : 'transparent'};
  ${(p) => p?.marked && 'box-shadow: 0 0 12px #0008;'}
  ${(p) => p?.status === 0 && pieceHover(p)}
`;

interface iPoint {
  status: number;
  onClick: () => void;
  _pointSize: number;
  _gameEnd?: boolean;
  next: number;
  marked?: boolean;
}

export const Point = ({
  status = 0,
  next,
  onClick,
  _pointSize = 50,
  marked = false,
  _gameEnd = false,
}: iPoint) => {
  return (
    <StyledPoint pointSize={_pointSize} marked={marked} gameEnd={_gameEnd}>
      <Stone
        status={status}
        onClick={onClick}
        next={next}
        marked={marked}
        borderSize={Number((_pointSize * 0.066).toFixed(1))}
      />
    </StyledPoint>
  );
};

export const StyledBoard = styled.div<any>`
  display: grid;
  grid-template-columns: repeat(
    ${(p) => p?.gameSize ?? 19},
    ${(p) => p?.pointSize ?? 50}px
  );
  justify-content: center;
  width: fit-content;
  height: fit-content;
  background-color: #f9f9f9;
`;

const StyledBoardWrapper = styled.div`
  justify-content: center;
  width: fit-content;
  height: fit-content;
  background-color: #fff;
`;

const BoardWrapper = styled.div<any>`
  opacity: ${(p) => (p.diff ? 0.36 : 1)};
  width: fit-content;
  height: fit-content;
  ${(p) => p.diff && `&:hover { opacity: 1; }`}
`;

interface iGoGame {
  infintyMode?: boolean;
}

type statusType = number[][];
type lastPieceType = [number, number] | null;
type turnType = number;

export const GoGame: FC<iGoGame> = ({ infintyMode = false }) => {
  const [
    ,
    {
      gameRule: rule,
      gameStatus: status,
      setGameStatus: setStatus,
      gameTurn: turn,
      setGameTurn: setTurn,
      pointSize,
      gameSize,
      setGameSize,
      isGameEnd,
      setIsGameEnd,
      gameStatusHistory,
      setGameStatusHistory,
      gameMarkedStatus: markedStatus,
      setGameMarkedStatus: setMarkedStatus,
    },
  ] = useStore();

  const { setResetScroll } = useContext(CallBackContext);

  // const [isGameEnd, setIsGameEnd] = useState(false);
  // const [markedStatus, setMarkedStatus] = useState<statusType>([]);

  // 
  useEffect(() => {
    if (!status.length || status.length === gameSize) return;
    setGameSize(status.length);
  }, [gameSize, setGameSize, status?.length]);

  // 
  useEffect(() => {
    if (turn <= 1) {
      setIsGameEnd(false);
      setMarkedStatus([]);
    }
  }, [setIsGameEnd, setMarkedStatus, turn]);

  // [] 
  const checkGo = (
    _status: statusType,
    _turn: turnType,
    _lastPiece: lastPieceType
  ): statusType | null => {
    // 
    if (!_lastPiece || _turn < 3) {
      return _status;
    }
    // 
    const { accessible, pointsStatus } = markPointsStatus(_status, _lastPiece);
    // 
    if (accessible === false) {
      return null;
    }
    // 
    const newstatus = _status.map((row) => [...row]);
    for (let i = 0; i < gameSize; i++) {
      for (let j = 0; j < gameSize; j++) {
        if (pointsStatus[i][j] === -1) {
          newstatus[i][j] = 0;
        }
      }
    }
    return newstatus;
  };

  // [] 
  const checkGomoku = (_status: statusType, _turn: turnType): statusType => {
    //  9 
    if (_turn < 5 * 2 - 1) return _status;
    // 
    const { gameEnd, pointsStatus } = markFive(
      _status,
      turn % 2 === 1 ? 'black' : 'white'
    );
    // 
    if (gameEnd) {
      setMarkedStatus(pointsStatus);
      setIsGameEnd(true);
    }
    return _status;
  };

  const nextPiece = turn % 2 === 1 ? 1 : -1;

  // 
  let clickableRef = useRef(true);
  useEffect(() => {
    clickableRef.current = true;
  });
  const putStone = (i: number, j: number) => {
    // 
    if (isGameEnd) return;
    // 
    if (status[i][j] !== 0) return;
    // 
    if (!clickableRef.current) return;
    clickableRef.current = false;

    let newStatus = status.map((row: number[]) => row.slice());
    newStatus[i][j] = nextPiece;
    const nextTurn = turn + 1;
    const nextLastPiece = [i, j] as lastPieceType;
    // 
    if (rule === 'gomoku') {
      // 
      newStatus = checkGomoku(newStatus, nextTurn);
    } else if (rule === 'go') {
      // 
      newStatus = checkGo(newStatus, nextTurn, nextLastPiece);
    }
    if (!newStatus) return;
    // 
    setStatus(newStatus);
    // +1
    setTurn(nextTurn);
    // 
    const newHistory = gameStatusHistory.slice(0, turn);
    newHistory[turn] = newStatus;
    setGameStatusHistory(newHistory);
  };
  const onPointClick = (i: number, j: number) => () => {
    putStone(i, j);
  };

  // 
  const board = (
    <StyledBoard gameSize={gameSize} pointSize={pointSize}>
      {status.map((row: number[], i: number) => {
        return row.map((col, j) => {
          return (
            <Point
              _pointSize={pointSize}
              key={`(${i},${j})`}
              status={col}
              next={nextPiece}
              onClick={onPointClick(i, j)}
              marked={markedStatus?.[i]?.[j] === 1}
              _gameEnd={isGameEnd}
            />
          );
        });
      })}
    </StyledBoard>
  );

  const scrollerOuterStyle = {
    // background: '#ffe',
    background: `repeating-linear-gradient(
      -38deg,
      #ececec,
      #ececec 4px,
      #f0f0f0 4px,
      #f0f0f0 8px
    )`,
  };

  return !status ? (
    <Loading style={{ ...scrollerOuterStyle, position: 'initial' }} />
  ) : (
    <LoopScroller
      surroundingBackup={infintyMode ? 2 : 1}
      outerStyle={scrollerOuterStyle}
      setResetCallBack={setResetScroll}
      sizeTrigger={pointSize}
    >
      <StyledBoardWrapper>
        <BoardWrapper diff={infintyMode}>{board}</BoardWrapper>
      </StyledBoardWrapper>
    </LoopScroller>
  );
};
