import { useCallback, useEffect, useState } from 'react';
import { defaultStorage, IndexedDBStorage } from '../utils/db';

/**
 * Hook for getting and setting data in IndexedDB.
 * @param storage The IndexedDBStorage instance to use.
 * @param key The key under which the data is stored.
 * @param defaultValue The default value to use if no data is found.
 */
export function useDb<T>(
  storage: IndexedDBStorage<T>,
  key: string,
  defaultValue: T
): [T, (newValue: T) => void, boolean] {
  const [data, setData] = useState(defaultValue);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const value = await storage.getItem(key);
        setData(value ?? defaultValue);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [storage, key, defaultValue]);

  const setValue = async (newValue: T) => {
    await storage.setItem(key, newValue);
    setData(newValue);
  };

  return [data, setValue, loading];
}

type DbObj<T> = {
  [key in keyof T]: [
    T[key],
    (newValue: T[key] | ((value: T[key]) => T[key])) => void
  ];
};

export const useDbObj = <T extends object, K extends keyof T>(
  storage: IndexedDBStorage<T>,
  objectStoreName: string,
  initialState: T
): { loading: boolean } & DbObj<T> => {
  const [loading, setLoading] = useState(true);
  const [state, setState] = useState<T>(initialState);

  useEffect(() => {
    storage
      .getItem(objectStoreName)
      .then((data) => {
        setState(data ?? initialState);
        setLoading(false);
      })
      .catch((error) => console.error(error));
  }, [storage, objectStoreName, initialState]);

  const setProperty = useCallback(
    (key: K, value: T[K] | ((value: T[K]) => T[K])) => {
      setState((prevState) => ({
        ...prevState,
        [key]:
          typeof value === 'function'
            ? (value as (value: T[K]) => T[K])(prevState[key])
            : value,
      }));
      storage
        .setItem(objectStoreName, { ...state, [key]: value })
        .catch((error) => console.error(error));
    },
    [storage, objectStoreName, state]
  );

  const dbObj = Object.keys(initialState).reduce(
    (acc, key) => ({
      ...acc,
      [key as K]: [
        state[key as K],
        (value: T[K] | ((value: T[K]) => T[K])) => setProperty(key as K, value),
      ],
    }),
    {} as DbObj<T>
  );

  return { loading, ...dbObj };
};

export const useDefaultDb = <T>(key: string, defaultValue: T) =>
  useDb(defaultStorage, key, defaultValue);

export const useDefaultDbObj = <T extends object>(
  key: string,
  defaultValue: T
) => useDbObj<T, any>(defaultStorage, key, defaultValue);
