import { setIsLog } from './log';

// 
export const array2DToString = (arr: number[][]) => {
  return (
    '\n' +
    arr
      .map((row) =>
        row.map((col) => (col >= 0 ? ' ' + col : '' + col)).join(' ')
      )
      .join('\n')
  );
};

// 
export const init2DArray = (m: number, n: number, value: any) =>
  Array(m)
    .fill(null)
    .map(() => Array(n).fill(value));

// 
const isOutOfBound = (m: number, n: number, i: number, j: number) =>
  i < 0 || i >= m || j < 0 || j >= n;

// 
const getNeighborsCood = (m: number, n: number, i: number, j: number) => {
  const neighbors = [
    [i - 1 >= 0 ? i - 1 : m - 1, j],
    [i + 1 < m ? i + 1 : 0, j],
    [i, j - 1 >= 0 ? j - 1 : n - 1],
    [i, j + 1 < n ? j + 1 : 0],
  ];
  return neighbors;
};

interface iAliveStatus {
  accessible: boolean;
  pointsStatus: number[][]; // 0: 1: -1: 
}

// 
export const markPointsStatus = (
  status: number[][],
  lastPiece: [number, number]
): iAliveStatus => {
  const m = status.length;
  const n = status[0].length;
  // 0: 1: -1: 2: , >2: 
  const pointsStatus = init2DArray(m, n, 2);
  let markedCount = 0;

  const mark = (i: number, j: number, value: number) => {
    if (isOutOfBound(m, n, i, j)) return;
    // 
    if (value < 2 && pointsStatus[i][j] < 2) return;
    pointsStatus[i][j] = value;
    if ([-1, 0, 1].includes(value)) {
      markedCount++;
    } else {
      markedCount--;
    }
  };

  const markAlive = (i: number, j: number) => {
    // 
    if (isOutOfBound(m, n, i, j)) return;
    if (status[i][j] === 0) {
      // 
      mark(i, j, 0);
      // 
      getNeighborsCood(m, n, i, j).forEach(([x, y]) => {
        // 
        if (status[x][y] === 0) return;
        mark(x, y, 1);
      });
    } else if (pointsStatus[i][j] === 1) {
      // 
      getNeighborsCood(m, n, i, j).forEach(([x, y]) => {
        // 
        if (pointsStatus[x][y] === 1) return;
        if (status[x][y] !== status[i][j]) return;
        mark(x, y, 1);
      });
    }
  };

  // 
  const markAllAlive = () => {
    while (markedCount < m * n) {
      const lastMarkedCount = markedCount;
      for (let i = 0; i < m; i++) {
        for (let j = 0; j < n; j++) {
          markAlive(i, j);
        }
      }
      // 
      if (lastMarkedCount === markedCount) break;
    }
  };

  // 
  const markUnknownDead = () => {
    for (let i = 0; i < m; i++) {
      for (let j = 0; j < n; j++) {
        if (pointsStatus[i][j] === 2) {
          mark(i, j, -1);
        }
      }
    }
  };

  // 
  const markConnected = (i: number, j: number, value: number) => {
    const tempMarkValue = 9;
    const findConnected = (i: number, j: number) => {
      // 
      mark(i, j, tempMarkValue);
      // 
      getNeighborsCood(m, n, i, j).forEach(([x, y]) => {
        if (isOutOfBound(m, n, x, y)) return;
        if (status[x][y] !== status[i][j]) return;
        if (pointsStatus[x][y] === tempMarkValue) return;
        mark(x, y, tempMarkValue);
        findConnected(x, y);
      });
    };

    findConnected(i, j);
    // 
    for (let i = 0; i < m; i++) {
      for (let j = 0; j < n; j++) {
        if (pointsStatus[i][j] === tempMarkValue) {
          mark(i, j, value);
        }
      }
    }
  };

  markAllAlive();
  markUnknownDead();
  let accessible = true;
  if (lastPiece) {
    const [i, j] = lastPiece;
    const isLastPieceDead = pointsStatus[i][j] === -1;
    if (isLastPieceDead) {
      const hasDeadNeighbor = getNeighborsCood(m, n, i, j).some(([x, y]) => {
        if (isOutOfBound(m, n, x, y)) return false;
        if (status[x][y] === status[i][j]) return false;
        return pointsStatus[x][y] === -1;
      });
      if (hasDeadNeighbor) {
        // 
        markConnected(i, j, 1);
      } else {
        // 
        accessible = false;
      }
    }
  }

  return {
    accessible,
    pointsStatus,
  };
};

export const getElementSize = (element: HTMLElement) => {
  const size = {
    height: 0,
    width: 0,
  };
  if (!element?.getBoundingClientRect) {
    return size;
  }
  const rect = element.getBoundingClientRect();
  size.height = rect.height;
  size.width = rect.width;
  return size;
};

const nextPoint =
  (m: number, n: number) => (i: number, j: number, direction: number[]) => {
    return [(m + i + direction[0]) % m, (n + j + direction[1]) % n];
  };

//  5 
export const markFive = (
  status: number[][],
  currentColor: 'black' | 'white'
) => {
  const color = currentColor === 'black' ? 1 : -1;
  const m = status.length;
  const n = status[0].length;
  // 0:  5 1:  5 
  const pointsStatus = init2DArray(m, n, 0);
  const getNextPoint = nextPoint(m, n);
  let gameEnd = false;
  const directions = [
    [0, 1], // 
    [1, 0], // 
    [1, 1], // 
    [1, -1], // 
  ];
  mainLoop: for (const direction of directions) {
    for (let i = 0; i < m; i++) {
      for (let j = 0; j < n; j++) {
        if (status[i][j] !== color) continue;
        let count = 1;
        let [x, y] = getNextPoint(i, j, direction);
        while (status[x][y] === color) {
          count++;
          [x, y] = getNextPoint(x, y, direction);
        }
        if (count >= 5) {
          let tempCood = [i, j];
          pointsStatus[i][j] = 1;
          for (let k = 1; k < count; k++) {
            const x = getNextPoint(tempCood[0], tempCood[1], direction)[0];
            const y = getNextPoint(tempCood[0], tempCood[1], direction)[1];
            pointsStatus[x][y] = 1;
            tempCood = [x, y];
          }
          gameEnd = true;
          break mainLoop;
        }
      }
    }
  }
  return { gameEnd, pointsStatus };
};

// 
let isDebugMode = false;
//  <style> 
const debugStyle = document.createElement('style');
debugStyle.innerHTML = `
#root .loop-scroll-container div.center {
  filter: brightness(0.4);
}
`;
debugStyle.dataset.debug = 'true';
export const toggleDebugMode = () => {
  if (!isDebugMode) {
    setIsLog(true);
    isDebugMode = true;
    document.head.appendChild(debugStyle);
    console.log('');
  } else {
    setIsLog(false);
    isDebugMode = false;
    document.querySelectorAll("[data-debug='true']").forEach((ele) => {
      document.head.removeChild(ele);
    });
    console.log('');
  }
};

const timeToTriggerDebugMode = 7;
let count: number = 0;
let timer: any = null;

export const triggerDebugMode = () => {
  // toggleDebugMode();
  clearTimeout(timer);
  count++;
  if (count >= timeToTriggerDebugMode) {
    toggleDebugMode();
    count = 0;
  } else {
    console.log(
      (isDebugMode ? '' : '') + '',
      timeToTriggerDebugMode - count,
      ''
    );
    timer = setTimeout(() => {
      count = 0;
    }, 500);
  }
};

export const math = {
  mod: (a: number, b: number) => {
    return ((a % b) + b) % b;
  },
};
